<template>
  <div class="content">
    <van-nav-bar
      title="商品详情"
      left-text="返回"
      right-text=""
      left-arrow
      @click-left="goBack"
    />
    <div class="pay_way_group">
      <div class="pay_way_title">商品介绍</div>
      <van-cell-group class="payment_group">
        <van-cell title="商品名称">{{order.goods_name}}</van-cell>
        <van-cell title="店铺名称">{{order.shop_name}}</van-cell>
        <van-cell title="商品主图">
          <img v-lazy="order.goods_pic" width="80%" @click="showImagePreview(order.goods_pic)">
        </van-cell>
        <van-cell title="关键字">{{order.keyword}}</van-cell>
        <van-cell title="商品规格">{{order.goods_sku}}</van-cell>
        <van-cell title="商品数量">{{order.goods_num}}</van-cell>
        <van-cell title="商品总价">{{order.money}}</van-cell>
        <van-cell title="实际下单金额">{{order.down_price}}</van-cell>
        <van-cell title="淘宝账号">{{order.tb_user || '无'}}</van-cell>
        <van-cell title="淘宝订单号">{{order.tb_order_sn || '无'}}</van-cell>
      </van-cell-group>

    </div>
    <div class="pay_way_group">
      <div class="pay_way_title">任务介绍</div>
      <van-cell-group class="payment_group">
        <van-cell title="浏览时间">{{plan.view_time}}</van-cell>
        <van-cell title="排名方式">{{plan.sort_type}} {{plan.sort_min}}~{{plan.sort_max}}</van-cell>
        <van-cell title="价格区间">{{plan.price_range || '无'}}</van-cell>
        <van-cell title="其他要求">{{plan.other_remark || '无'}}</van-cell>
      </van-cell-group>
    </div>
    <div class="pay_way_group">
      <div class="pay_way_title">实际下单金额:<span style="color: darkred;"></span></div>
      <van-cell-group class="payment_group">
        <van-field v-model="down_price" type="text"  placeholder="请输入实际付款金额，留空为商品总价"/>
      </van-cell-group>
    </div>
    <div class="pay_way_group">
      <div class="pay_way_title">其他:<span style="color: darkred;">可留空</span></div>
      <van-cell-group class="payment_group">
        <van-field v-model="tb_user" type="text"  placeholder="请输入淘宝账号"/>
      </van-cell-group>
      <van-cell-group class="payment_group">
        <van-field v-model="tb_order_sn" type="text"  placeholder="请输入淘宝订单号"/>
      </van-cell-group>
      <van-cell-group class="payment_group">
        <van-field v-model="remark" type="text"  placeholder="请输入备注"/>
      </van-cell-group>
    </div>
    <van-button class="pay_submit" @click="submit" :loading="isSubmit" type="danger" :bottom-action="true">提交更新
    </van-button>
    <van-button class="pay_submit" type="warning" @click="goBack" :bottom-action="true" style="margin-top:10px;">返回任务
    </van-button>
  </div>
</template>

<script>
  import {
    Row,
    Col,
    CellGroup,
    Cell,
    Button,
    Dialog,
    ImagePreview,
    Toast,
    NavBar,
    Field
  } from "vant";
  import { ORDER_VIEW,UPDATE_ORDER} from "@/api/task";

  export default {
    name: "orderViews",
    data() {
      return {
        isSubmit : false,
        order:{
          "id": "",
          "shop_name": "",
          "goods_name": "",
          "goods_num": "",
          "goods_sku": "",
          "goods_pic": "",
          "money": "",
          "down_price": "",
          "keyword": "",
          "tb_user": "",
          "tb_order_sn": "",
          "remark":"",
        },
        plan: {
          "id": "",
          "sort_type": "",
          "sort_min": "",
          "sort_max": "",
          "view_time": "",
          "price_range": "",
          "other_remark": "",
        }
      };
    },
    props: {
      id: this.id
    },
    watch:{
			'$route': 'changeRouter'
		},
    created(){
      this.initPage();
    },

    methods: {
      initPage(){
        if(!this.id){
          return;
        }
        this.$reqGet(ORDER_VIEW,{id:this.id}).then((res) => {
          this.getSuccess(res);
        }).catch((err) => {
          this.getSuccess(err);
        });
      },
      getSuccess(res){
        let result = res.data;
        if(!result || result.code != 0){
          let msg = result.msg ? result.msg : "获取商品失败";
          Toast.fail(msg);
          return;
        }
        let data = result.data;
        this.order = data.order;
        this.plan = data.plan;
      },
      submit() {
        let formdata = new FormData();
        formdata.append("id", this.id);
        formdata.append("down_price", this.down_price);
        formdata.append("tb_user", this.tb_user);
        formdata.append("tb_order_sn", this.tb_order_sn);
        formdata.append("remark", this.remark);
        this.isSubmit=true;
        this.$reqPost(UPDATE_ORDER, formdata).then((res) => {
          this.submitSuccess(res);
        }).catch((err) => {

        });
      },
      submitSuccess(res) {
        let result = res.data;
        if(result.code!=0){
          this.isSubmit=false;
          Toast(result.msg);
          return;
        }
        Toast.success(result.msg);
        setTimeout(() => {
          this.isSubmit=false;
          this.$router.go(-1)
        }, 1000)
      },
      //返回
      goBack() {
        this.$router.go(-1)
      },

      showImagePreview(img){
        ImagePreview([img]);
      },

    },

    components: {
      [Row.name]: Row,
      [Col.name]: Col,
      [CellGroup.name]: CellGroup,
      [Cell.name]: Cell,
      [Button.name]: Button,
      [ImagePreview.name]: ImagePreview,
      [Toast.name]: Toast,
      [NavBar.name]:NavBar,
      [Field.name]:Field
    },

    filters: {
      money: function(val) {
        val = val.toString().replace(/\$|\,/g,'');
        if(isNaN(val)) {
          val = "0";
        }
        let sign = (val == (val = Math.abs(val)));
        val = Math.floor(val*100+0.50000000001);
        let cents = val%100;
        val = Math.floor(val/100).toString();
        if(cents<10) {
          cents = "0" + cents
        }
        for (var i = 0; i < Math.floor((val.length-(1+i))/3); i++) {
          val = val.substring(0,val.length-(4*i+3))+',' + val.substring(val.length-(4*i+3));
        }
        return (((sign)?'':'-') + val + '.' + cents);
      }
    }
  };

</script>

<style lang="scss" scoped>

  // .payment_group {
  //   /*padding: 10px 15px;*/
  // }

  .task-countdown {
    text-align: center;
    font-size: 12px;
    background-color: #fffeec;
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    line-height: 30px;
    z-index: 999;
  }

  .pay_submit {
    /*position: fixed;*/
    /*bottom: 0;*/
    width: 100%;
  }

  .cannel_submit{
    margin-top:10px;
    width: 100%;
  }

  .pay_way_group{
    background-color: #ffffff;
    margin-bottom: 10px;
  }

  .pay_way_group img {
    vertical-align: middle;
  }

  .pay_way_title {
    font-weight: bold;
    padding: 15px;
    background-color: #fff;
  }

  .upload_hint{
    position: relative;
    padding-left: 20px;
    line-height: 1.6;
    margin: 10px;
    i{
      position: absolute;
      top: 2px;
      left: 0;
    }
  }

  .vacancy_row{
    .van-col {
      font-size: 13px;
      line-height: 30px;
      text-align: center;
      vertical-align: middle;
      margin-bottom: 0px;
      background-clip: content-box;
    }
  }

  .countdown{
    text-align: center;
    width: 100%;
    height: 50px;
    line-height: 50px;
    border: 0;
    border-radius: 0;
    font-size: 16px;
    color: #fff;
    background-color: #29a0f6;
  }

  .van-cell__right-icon {
    top: 3px;
  }

  .text_style {
    color: #29a0f6;
    font-size: 12px;
    padding-left: 15px;
    text-align: left;
    margin: 0;
    padding-bottom: 7px;
  }
  .text_red{
    color:red;
    font-size: 12px;
    padding-left: 15px;
    text-align: left;
    margin: 0;
    padding-bottom: 7px;
  }
  .no-longtap{
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
  }
</style>



